import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import Homepage from '@/pages/homepage/Homepage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homepage',
      component: Homepage,
      meta: {title: 'Matheus Almeida'}
    }
  ]
})



